import themeColor from './themeColor'
import {message} from 'ant-design-vue';

const colorList = [
    {
        key: '', color: '#F5222D'
    },
    {
        key: '', color: '#FA541C'
    },
    {
        key: '', color: '#FAAD14'
    },
    {
        key: '', color: '#13C2C2'
    },
    {
        key: '', color: '#52C41A'
    },
    {
        key: '', color: '#1890FF'
    },
    {
        key: '', color: '#2F54EB'
    },
    {
        key: '', color: '#722ED1'
    }
]

const updateTheme = (newPrimaryColor: string) => {
    const hideMessage = message.loading('', 0)
    themeColor.changeColor(newPrimaryColor).finally(() => {
        setTimeout(() => {
            hideMessage()
        })
    })
}


export {updateTheme, colorList}